:root {
  --color-active: #00acdc;
}

*, :after, :before {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__item + .nav__item {
  margin-left: 20px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .2s ease-in;
}

.nav__link:hover {
  color: var(--color-active);
}

.is-active {
  color: var(--color-active);
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  border-bottom: var(--color-active) 4px solid;
  border-radius: 8px;
  width: 100%;
  position: absolute;
  bottom: 0;
}
